<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-left">
        <ctitle :title="'网信信访量信访终端占比'"></ctitle>
        <div class="detail">
          <allCount class="all-count" :count="allcount"></allCount>
          <div class="inner">
            <detailList :params="detailList" :breakNumber="3"></detailList>
          </div>
        </div>
        <div class="chart">
          <pie-chart3D :params="zbPieParams"></pie-chart3D>
        </div>
      </div>
      <div class="c-right">
        <ctitle :title="'网信信访量信访终端走势'"></ctitle>
        <div class="r-top">
          <div class="zs-item" v-loading="!zsDetailList.length"
               :element-loading-background="$STANDARD.loadingBgColor">
            <p
              v-for="(item, index) in zsDetailList.slice(0,3)" :key="index">
              <label>{{item.name}}</label>
              <span class="inner">
                <label>同比</label>
                <span
                  class="countSymbol"
                  v-if="item.tb > 0"
                >+</span>
                <incNumber
                  class="number"
                  :incNumber="item.tb"
                ></incNumber>
                <span class="unit">%</span>
              </span>
              <span class="inner">
                <label>环比</label>
                <span
                  class="countSymbol"
                  v-if="item.hb > 0"
                >+</span>
                <incNumber
                  class="number"
                  :incNumber="item.hb"
                ></incNumber>
                <span class="unit">%</span>
              </span>
            </p>
          </div>
          <div  class="zs-item" v-loading="!zsDetailList.length"
                :element-loading-background="$STANDARD.loadingBgColor">
            <p v-if="zsDetailList.length" v-for="(item, index) in zsDetailList.slice(3,6)" :key="index">
              <label>{{item.name}}</label>
              <span class="inner">
                <label>同比</label>
                <span
                  class="countSymbol"
                  v-if="item.tb > 0"
                >+</span>
                <incNumber
                  class="number"
                  :incNumber="item.tb"
                ></incNumber>
                <span class="unit">%</span>
              </span>
              <span class="inner">
                <label>环比</label>
                <span
                  class="countSymbol"
                  v-if="item.hb > 0"
                >+</span>
                <incNumber
                  class="number"
                  :incNumber="item.hb"
                ></incNumber>
                <span class="unit">%</span>
              </span>
            </p>
          </div>
        </div>
        <div class="r-bottom">
          <LinesR :params="zsLineParams"></LinesR>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import {getCurMonth} from "@/utils/time";
  import ctitle from "@/components/title/index";
  import incNumber from "@/components/incNumber/index";
  import PieChart3D from "@/components/pie/PieChart3D";
  import LinesR from "@/components/line/LinesR";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";

  export default {
    name: "wtzd",
    components: {
      ctitle,
      incNumber,
      PieChart3D,
      LinesR,
      allCount,
      detailList
    },
    data() {
      return {
        // 信访终端占比
        allcount: -1,
        detailList: null,
        zbPieParams: {
          data: [],
          angle: 20,
          radius: "45%",
          labelRadius: 25,
          color: this.$STANDARD.pieColors,
          styleObj: {
            width: "100%",
            height: "100%"
          }
        },
        // 信访终端走势
        zsDetailList: [],
        zsLineParams: {
          data: [],
          colors: [
            {
              line: "rgba(19, 240, 255, 1)",
              start: "rgba(19, 240, 255, .5)",
              end: "rgba(19, 240, 255, .5)"
            },
            {
              line: "rgba(78, 237, 153, 1)",
              start: "rgba(78, 237, 153, .5)",
              end: "rgba(78, 237, 153, .5)",
            },
            {
              line: "rgba(217, 62, 117, 1)",
              start: "rgba(217, 62, 117, .5)",
              end: "rgba(217, 62, 117, .5)",
            },
            {
              line: "rgba(204, 190, 81, 1)",
              start: "rgba(204, 190, 81, .5)",
              end: "rgba(204, 190, 81, .5)"
            },
            {
              line: "rgba(235, 144, 2, 1)",
              start: "rgba(235, 144, 2, .5)",
              end: "rgba(235, 144, 2, .5)",
            },
            {
              line: "rgba(27, 161, 255, 1)",
              start: "rgba(27, 161, 255, .5)",
              end: "rgba(27, 161, 255, .5)",
            }
          ],
          option: {
            axisLabelColor: "#fff",
            yaxisLabelColor: "#82d8ff",
            legendfontSize: 18,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "",
            axisYName: "件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "5%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "0%",
            legendRight: "10%",
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        }
      };
    },
    mounted() {
      this.getAPI(this.date);
    },
    methods: {
      getAPI(date) {
        // 1.信访终端占比（总数）
        get_http("xfxsWxWxzdZbTotal", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.信访终端占比（各类型）
        get_http("xfxsWxWxzdZbType", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.detailList = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.信访终端占比（饼图）
        get_http("xfxsWxWxzdZbPie", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.zbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4.信访终端走势（各类型）
        get_http("xfxsWxWxzdZsType", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            let newData=[],tbKey = 0, hbKey = 0;
            for(let key in res.data.map){
              if(res.data.map[key]==='同比'){
                tbKey=key
              }
              if(res.data.map[key]==='环比'){
                hbKey=key
              }
            }

            for(let item of res.data.data.values()){
              let obj = {
                name:item.name,
                tb:item[tbKey],
                hb:item[hbKey]
              };
              newData.push(obj);
            }
            this.zsDetailList =newData;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.信访终端走势（折线）
        get_http("xfxsWxWxzdZsLine", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.zsLineParams.map = res.data.map;
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box

      .c-left
        flex 1
        height 100%
        position relative
        display flex
        flex-direction column
        border-right 1px dashed #91e3f7

        .detail
          flex 1
          box-sizing border-box
          display flex
          justify-content space-evenly
          align-items center

          .all-count
            flex 1

          .inner
            flex 2.5
            display flex
            height 100%
            .detail-list
              flex 1

        .chart
          width 100%
          flex 1

      .c-right
        flex 1
        height 100%
        position relative
        display flex
        flex-direction column

        .r-top
          flex 1
          position relative
          display flex
          align-items center
          justify-content center

          .zs-item
            flex 1
            height 100%
            display flex
            flex-direction column
            justify-content center
            align-items center
            p
              height 0.7rem
              display flex
              justify-content center
              align-items center
              .inner
                margin 0 .2rem
              > label
                width 1rem
                text-align right
                color #fff
                font-size 0.16rem

              span
                flex 1
                color #ffde00
                font-size 0

                label
                  width auto
                  color #00b0ff
                  font-size 0.16rem
                  margin-right .1rem

                .countSymbol
                  font-size 0.16rem

                .number
                  font-size 0.16rem

                .unit
                  font-size 0.16rem

        .r-bottom
          flex 1
</style>
